import React, { useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import { useAuth } from "../context/AuthContext";
import GoogleButton from 'react-google-button'; // 1. Google Button- 
import "./auth.scss";

const Login = () => {
  const navigate = useNavigate();
  // 2. AuthContext-  login   registerWithGoogle-
  const { login, registerWithGoogle } = useAuth(); 
  
  const [formData, setFormData] = useState({ email: "", password: "" });

  const handleSubmit = (e) => {
    e.preventDefault();
    // 
    if (!formData.email || !formData.password) {
        alert("  ");
        return;
    }
    login(formData.email, formData.password);
    navigate("/");
  };
  
  // 3. Google-  
  const handleGoogleLogin = () => {
    //    ,    
    //  registerWithGoogle ,   /
    registerWithGoogle(); 
    navigate("/");
  };

  return (
    <div className="auth-container fade-in-image">
      <div className="auth-card">
        <h2></h2>
        
        {/* Google-  */}
        <div className="google-auth-section mb-4 d-flex justify-content-center">
            <GoogleButton
                label=" Google-" //   ""
                onClick={handleGoogleLogin}
                style={{ width: '100%' }}
            />
        </div>

        {/*   */}
        <div className="divider-or mb-4"><span></span></div>
        
        <form onSubmit={handleSubmit}>
          <div className="form-group">
            <label>-</label>
            <input 
              type="email" 
              onChange={(e) => setFormData({...formData, email: e.target.value})} 
              placeholder="example@mail.com" 
              required
            />
          </div>
          <div className="form-group">
            <label></label>
            <input 
              type="password" 
              onChange={(e) => setFormData({...formData, password: e.target.value})} 
              placeholder="******" 
              required
            />
          </div>
          
          <button type="submit" className="btn-auth"></button>
        </form>
        
        <div className="auth-footer">
            ? <Link to="/register"></Link>
        </div>
      </div>
    </div>
  );
};

export default Login;