import React, { useState, useEffect } from "react";
import { useAuth } from "../context/AuthContext";
import "./pricing.scss";

const pricingPlans = [
  { 
    id: 1, 
    title: "", 
    price: "7 ", 
    credits: 10, 
    features: [
      { text: "10 ", status: 'active' }, 
      { text: "", status: 'disabled' },
      { text: "", status: 'disabled' }
    ] 
  },
  { 
    id: 2, 
    title: " (BEST)", 
    price: "17 ", 
    credits: 30, 
    features: [
      { text: "30 ", status: 'active' }, 
      { text: " (6 )", status: 'active' }, 
      { text: " (8 )", status: 'active' }, 
      { text: "  ", status: 'active' }
    ] 
  },
  { 
    id: 3, 
    title: " VIP", 
    price: "27 ", 
    credits: 9999, 
    features: [
      { text: " ", status: 'active' }, 
      { text: "", status: 'active' }, 
      { text: "", status: 'active' }
    ] 
  },
];

const Pricing = () => {
  const { user } = useAuth();
  const [currentCredits, setCurrentCredits] = useState(0);

  useEffect(() => {
    const credits = parseInt(localStorage.getItem("genderApp_credits") || "0");
    setCurrentCredits(credits);
  }, [user]);

  const handlePurchase = (plan) => {
    if (!user) {
      return alert("    .");
    }
    
    const confirmPurchase = window.confirm(
      `  ${plan.title}   (${plan.price})?`
    );

    if (confirmPurchase) {
      const newTotalCredits = currentCredits + plan.credits;
      localStorage.setItem("genderApp_credits", newTotalCredits.toString());
      setCurrentCredits(newTotalCredits);
      
      alert(`${plan.credits}   !  : ${newTotalCredits}`);

      if (plan.title.includes("VIP")) {
         localStorage.setItem("genderApp_isPremium", "true");
      }
    }
  };

  return (
    <div className="pricing-page fade-in-image">
      <div className="container" style={{maxWidth: '1200px'}}>
        <header className="text-center mb-5">
          <h2 className="text-center"></h2>
          <p className="text-center text-muted">        .</p>
          <div className="current-balance mt-4 p-3 bg-light rounded shadow-sm">
              : <strong>{currentCredits} </strong>
          </div>
        </header>

        <div className="row g-4 justify-content-center">
          {pricingPlans.map((plan) => (
            <div key={plan.id} className="col-lg-4 col-md-6">
              <div className={`price-card card text-center ${plan.title.includes('BEST') ? 'best-value' : ''}`}>
                
                {plan.title.includes('BEST') && <div className="badge-best"> </div>}

                <div className="card-body p-4">
                  <h3 className="card-title">{plan.title}</h3>
                  <div className="price-tag my-3">
                    <span className="display-5 fw-bold">{plan.price}</span>
                  </div>
                  
                  <ul className="list-unstyled text-start mx-auto" style={{maxWidth: '250px'}}>
                    {plan.features.map((feature, index) => (
                      <li key={index} className={`mb-2 feature-${feature.status}`}>
                        {feature.status === 'active' ? (
                          <>
                            <i className="bi bi-check-circle-fill me-2" style={{color: '#5de46fff'}}></i> {feature.text}
                          </>
                        ) : (
                          <>
                            <i className="bi bi-x-circle-fill me-2" style={{color: '#dd4e4eff'}}></i> 
                            <del>{feature.text}</del>
                          </>
                        )}
                      </li>
                    ))}
                  </ul>

                  <button 
                    className={`btn w-100 mt-4 ${plan.title.includes('BEST') ? 'btn-primary' : 'btn-outline-secondary'}`}
                    onClick={() => handlePurchase(plan)}
                  >
                    
                  </button>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Pricing;