import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import { calculateGenderLogic } from "../../assets/js/genderLogic";
import { useAuth } from "../context/AuthContext"; 
import "./genderCalculator.scss";

// --- NEW IMPORTS ---
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css"; // datepicker- 
import kaLocale from "../locales/ka"; //   
// -------------------

import boyImage from "../../assets/img/boy.png";
import girlImage from "../../assets/img/girl.png";

const GenderCalculator = () => {
  const navigate = useNavigate();
  const { user } = useAuth(); 
  
  const [formData, setFormData] = useState({
    momDob: "",
    dadDob: "",
    conceptionDate: "",
    momHasSurgery: false,
    momSurgeryDate: "",
    dadHasSurgery: false,
    dadSurgeryDate: "",
  });

  const [result, setResult] = useState(null);
  const [credits, setCredits] = useState(0); 
  const [showPaywall, setShowPaywall] = useState(false); 

  useEffect(() => {
    const savedCredits = localStorage.getItem("genderApp_credits");
    if (savedCredits === null && user) {
      setCredits(1); 
      localStorage.setItem("genderApp_credits", "1");
    } else if (savedCredits !== null) {
      setCredits(parseInt(savedCredits));
    }
  }, [user]);

  // --- NEW: DatePicker-   ---
  const handleDateChange = (date, name) => {
    // date-fns-   DatePicker  Date .
    //    'YYYY-MM-DD' ,    .
    const formattedDate = date ? date.toISOString().split('T')[0] : '';
    setFormData(prevData => ({
        ...prevData,
        [name]: formattedDate,
    }));
  };

  //   (   input-)
  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: type === "checkbox" ? checked : value,
    }));
  };

  const handleCalculate = () => {
    if (!user) {
      navigate("/login");
      return;
    }
    if (!formData.momDob || !formData.dadDob || !formData.conceptionDate) {
      alert(",   !");
      return;
    }
    if (credits <= 0) {
      setShowPaywall(true); 
      return;
    }

    const calculationResult = calculateGenderLogic(formData);
    setResult(calculationResult);
    const newCredits = credits - 1;
    setCredits(newCredits);
    localStorage.setItem("genderApp_credits", newCredits.toString());
  };

  return (
    <div className="card calculatorCard fade-in-image position-relative">
      
      {user && (
        <div 
            // style={{
            //     position: 'absolute', 
            //     top: '15px', 
            //     right: '15px', 
            //     background: '#ffd700', 
            //     color: '#333',
            //     padding: '5px 12px', 
            //     borderRadius: '20px', 
            //     fontSize: '13px', 
            //     fontWeight: 'bold',
            //     zIndex: 5,
            //     boxShadow: '0 2px 5px rgba(0,0,0,0.1)'
            // }}
            >
            {/*  : {credits} */}
        </div>
      )}

      {/* --- PAYWALL ( ) --- ( ) */}
      {showPaywall && (
        <div className="paywall-overlay fade-in-image">
          <h2>  </h2>
          <p> : 0</p>
          <p>   .</p>
          
          <button 
            className="btn btn-success mb-3" 
            onClick={() => navigate('/pricing')} 
            style={{padding: "10px 30px", fontSize: "18px", borderRadius: "25px"}}
          >
             
          </button>
          
          <button 
            className="btn btn-link text-muted" 
            onClick={() => setShowPaywall(false)}
            style={{textDecoration: 'none', fontSize: '14px'}}
          >
             
          </button>
        </div>
      )}

      {!result && (
        <div className="form-block">
          <h2 className="text-center"> </h2>
          
          {!user && (
             <div className="alert alert-warning text-center small p-2 mb-3">
                    <Link to="/login"><b></b></Link>
             </div>
          )}

          <p className="mutedtext">
            : ,    , , 
             . ,    .
          </p>

          {/*  - INPUT  DatePicker- */}
          <div className="labelBox">
            <label className="form-label">  :</label>
            <DatePicker
                // DatePicker  Date .  ,  null-
                selected={formData.momDob ? new Date(formData.momDob) : null}
                onChange={(date) => handleDateChange(date, 'momDob')}
                dateFormat="dd.MM.yyyy"
                placeholderText=" /  / "
                locale={kaLocale} // ** **
                className="form-control mb-2 react-datepicker-custom"
                showYearDropdown
                dropdownMode="select"
            />
            
            <div className="form-check">
              <input
                type="checkbox"
                className="form-check-input"
                id="momCheck"
                name="momHasSurgery"
                checked={formData.momHasSurgery}
                onChange={handleChange}
              />
              <label className="form-check-label small" htmlFor="momCheck">
                 ? /  ?
              </label>
            </div>
            {formData.momHasSurgery && (
              <div className="mt-2 pt-2 addInput">
                <label className="form-label small">
                       :
                  <p className="mute-text text-danger small mb-0">
                      ,    400- !
                  </p>
                </label>
                <DatePicker
                    selected={formData.momSurgeryDate ? new Date(formData.momSurgeryDate) : null}
                    onChange={(date) => handleDateChange(date, 'momSurgeryDate')}
                    dateFormat="dd.MM.yyyy"
                    placeholderText=" /  / "
                    locale={kaLocale}
                    className="form-control"
                    showYearDropdown
                    dropdownMode="select"
                />
              </div>
            )}
          </div>

          {/*  - INPUT  DatePicker- */}
          <div className="labelBox">
            <label className="form-label">  :</label>
            <DatePicker
                selected={formData.dadDob ? new Date(formData.dadDob) : null}
                onChange={(date) => handleDateChange(date, 'dadDob')}
                dateFormat="dd.MM.yyyy"
                placeholderText=" /  / "
                locale={kaLocale} 
                className="form-control mb-2"
                showYearDropdown
                dropdownMode="select"
            />
            <div className="form-check">
              <input
                type="checkbox"
                className="form-check-input"
                id="dadCheck"
                name="dadHasSurgery"
                checked={formData.dadHasSurgery}
                onChange={handleChange}
              />
              <label className="form-check-label textcustom" htmlFor="dadCheck">
                 ? /  ?
              </label>
            </div>
            {formData.dadHasSurgery && (
              <div className="mt-2 pt-2 addInput">
                <label className="form-label small">
                       :
                  <p className="mute-text text-danger small mb-0">
                      ,    400- !
                  </p>
                </label>
                <DatePicker
                    selected={formData.dadSurgeryDate ? new Date(formData.dadSurgeryDate) : null}
                    onChange={(date) => handleDateChange(date, 'dadSurgeryDate')}
                    dateFormat="dd.MM.yyyy"
                    placeholderText=" /  / "
                    locale={kaLocale} 
                    className="form-control form-control-sm"
                    showYearDropdown
                    dropdownMode="select"
                />
              </div>
            )}
          </div>

          {/*  - INPUT  DatePicker- */}
          <div className="mb-4">
            <label className="form-label h6">
                ():
            </label>
            <DatePicker
                selected={formData.conceptionDate ? new Date(formData.conceptionDate) : null}
                onChange={(date) => handleDateChange(date, 'conceptionDate')}
                dateFormat="dd.MM.yyyy"
                placeholderText=" /  / "
                locale={kaLocale} 
                className="form-control form-control-lg"
                showYearDropdown
                dropdownMode="select"
            />
          </div>

          <button
            className={`btn w-100 ${user ? 'btnForCalculate' : 'btn-secondary'}`} 
            onClick={handleCalculate}
          >
            {!user ? "  " : credits > 0 ? " (1 )" : ""}
          </button>
        </div>
      )}

      {/* ---   --- ( ) */}
      {result && result.resultType === "boy" && (
        <div className="result-image-container text-center">
          <img src={boyImage} alt="It's a boy!" />
          <button className="btn btnForCalculate mt-3" onClick={() => { setResult(null); setShowPaywall(false); }}></button>
        </div>
      )}
      {result && result.resultType === "girl" && (
        <div className="result-image-container text-center">
          <img src={girlImage} alt="It's a girl!" />
          <button className="btn btnForCalculate mt-3" onClick={() => { setResult(null); setShowPaywall(false); }}></button>
        </div>
      )}
      {result && result.resultType === "unknown" && (
        <div className="text-result-box">
          <h3 className="text-center text-muted mb-4"></h3>
          <div className={`card mb-3 border-unknown`}>
            <div className="card-body p-3 bg-light">
              <span className="d-block small fw-bold text-muted">  :</span>
              <span className={`fs-5 fw-bold text-unknown`}>{result.details.bloodText}</span>
            </div>
          </div>
          <div className={`card mb-4 border-${result.details.chineseClass}`}>
            <div className="card-body p-3 bg-light">
              <span className="d-block small fw-bold text-muted">  :</span>
              <span className={`fs-5 fw-bold text-${result.details.chineseClass}`}>{result.details.chineseText}</span>
            </div>
          </div>
          <button className="btn btn-secondary w-100" onClick={() => { setResult(null); setShowPaywall(false); }}> </button>
        </div>
      )}
    </div>
  );
};

export default GenderCalculator;