import React, { useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import { useAuth } from "../context/AuthContext";
import GoogleButton from 'react-google-button';
import "./auth.scss";

const Register = () => {
  const navigate = useNavigate();
  //  Google-   AuthContext-
  const { register, registerWithGoogle } = useAuth(); 
  
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    password: ""
  });

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!formData.name || !formData.email || !formData.password) {
      alert("  ");
      return;
    }
    
    register(formData);
    navigate("/");
  };
  
  // 2. Google-  
  const handleGoogleRegister = () => {
    //      AuthContext- ( )
    registerWithGoogle(); 
    navigate("/");
  };

  return (
    <div className="auth-container fade-in-image">
      <div className="auth-card">
        <h2></h2>
        {/* <p> ,   </p> */}
        
        {/* Google-  */}
        <div className="google-auth-section mb-4 d-flex justify-content-center">
            <GoogleButton
                label=" Google-"
                onClick={handleGoogleRegister}
                style={{ width: '100%' }}
            />
        </div>

        {/*   */}
        <div className="divider-or mb-4"><span></span></div>
        
        <form onSubmit={handleSubmit}>
          <div className="form-group">
            <label></label>
            <input type="text" name="name" onChange={handleChange} placeholder=" " />
          </div>
          <div className="form-group">
            <label>-</label>
            <input type="email" name="email" onChange={handleChange} placeholder="example@mail.com" />
          </div>
          <div className="form-group">
            <label></label>
            <input type="password" name="password" onChange={handleChange} placeholder="******" />
          </div>
          
          <button type="submit" className="btn-auth"></button>
        </form>
        
        <div className="auth-footer">
            ? <Link to="/login"></Link>
        </div>
      </div>
    </div>
  );
};

export default Register;