import React, { createContext, useState, useEffect, useContext } from "react";

const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);

  //  ,      
  useEffect(() => {
    const storedUser = localStorage.getItem("genderApp_user");
    if (storedUser) {
      setUser(JSON.parse(storedUser));
      
      //      , 
      //     user  
    }
  }, []);

  //   (-)
  const register = (userData) => {
    //      
    //  1 
    const newUser = { ...userData, id: Date.now(), credits: 1 }; 
    setUser(newUser);
    localStorage.setItem("genderApp_user", JSON.stringify(newUser));
    localStorage.setItem("genderApp_credits", "1"); //   
  };

  // --- NEW: Google-   ---
  const registerWithGoogle = () => {
    //   ,     
    const googleUser = {
        name: "Google ",
        email: "google_user@gmail.com", 
        id: Date.now(),
        credits: 1 //  1 
    };
    
    setUser(googleUser);
    localStorage.setItem("genderApp_user", JSON.stringify(googleUser));
    localStorage.setItem("genderApp_credits", "1");
  };
  // ------------------------------------------------

  //  
  const login = (email, password) => {
    //   .    
    //  ,    
    const credits = localStorage.getItem("genderApp_credits") || "1";
    
    const loggedInUser = { 
        name: " ", 
        email: email, 
        credits: parseInt(credits) 
    };
    
    setUser(loggedInUser);
    localStorage.setItem("genderApp_user", JSON.stringify(loggedInUser));
  };

  //  
  const logout = () => {
    //   ,        
    setUser(null);
    localStorage.removeItem("genderApp_user");
  };

  return (
    <AuthContext.Provider value={{ user, register, login, logout, registerWithGoogle }}> {/*  registerWithGoogle */}
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => useContext(AuthContext);