import React, { useState } from "react";
import { calculateGenderLogic } from "../../assets/js/genderLogic";
import "./genderCalendar.scss";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import kaLocale from "../locales/ka";

const GenderCalendar = () => {
  const [parentsData, setParentsData] = useState({ momDob: "", dadDob: "" });
  const [duration, setDuration] = useState(6); // 6, 24,  60 
  const [calendarData, setCalendarData] = useState(null);

  // --- DatePicker-   ---
  const handleDateChange = (date, name) => {
    const formattedDate = date ? date.toISOString().split('T')[0] : '';
    setParentsData(prevData => ({
        ...prevData,
        [name]: formattedDate,
    }));
  };
  
  const handleGenerate = () => {
    if (!parentsData.momDob || !parentsData.dadDob) return alert("  ");

    let results = [];
    const today = new Date();

    for (let i = 0; i < duration; i++) {
      let futureDate = new Date(today.getFullYear(), today.getMonth() + i, 15);
      
      const tempFormData = {
        momDob: parentsData.momDob,
        dadDob: parentsData.dadDob,
        conceptionDate: futureDate.toISOString().split('T')[0],
        momHasSurgery: false, 
        dadHasSurgery: false
      };

      const res = calculateGenderLogic(tempFormData);
      
      results.push({
        date: futureDate,
        monthName: futureDate.toLocaleString('ka-GE', { month: 'long', year: 'numeric' }),
        type: res.resultType,
        details: res.details
      });
    }
    setCalendarData(results);
  };

  return (
    <div className="card calendarCard fade-in-image">
      <h2 className="text-center"></h2>
      <p className="text-center text-muted">        </p>

      {/*   ( ) */}
      <div className="row mt-4">
        <div className="col-md-6 mb-3">
            <label>  :</label>
            <DatePicker
                selected={parentsData.momDob ? new Date(parentsData.momDob) : null}
                onChange={(date) => handleDateChange(date, 'momDob')}
                dateFormat="dd.MM.yyyy"
                placeholderText=" /  / "
                locale={kaLocale} 
                className="form-control"
                showYearDropdown
                dropdownMode="select"
            />
        </div>
        <div className="col-md-6 mb-3">
            <label>  :</label>
            <DatePicker
                selected={parentsData.dadDob ? new Date(parentsData.dadDob) : null}
                onChange={(date) => handleDateChange(date, 'dadDob')}
                dateFormat="dd.MM.yyyy"
                placeholderText=" /  / "
                locale={kaLocale} 
                className="form-control"
                showYearDropdown
                dropdownMode="select"
            />
        </div>
      </div>

      {/*   - NEW STYLING */}
      <div className="duration-buttons mt-3 text-center">
          <button 
              className={`duration-btn ${duration === 6 ? 'active-duration' : ''}`} 
              onClick={() => setDuration(6)}>
              6 
          </button>
          <button 
              className={`duration-btn ${duration === 24 ? 'active-duration' : ''}`} 
              onClick={() => setDuration(24)}>
              2 
          </button>
          <button 
              className={`duration-btn ${duration === 60 ? 'active-duration' : ''}`} 
              onClick={() => setDuration(60)}>
              5 
          </button>
      </div>

      <button className="btn btnForCalculate w-100 mt-3" onClick={handleGenerate}> </button>

      {/*   ( ) */}
      {calendarData && (
        <div className="calendar-grid mt-4">
            {calendarData.map((item, index) => (
                <div key={index} className={`calendar-month ${item.type}`}>
                    <span className="month-label">{item.monthName}</span>
                    <span className="gender-icon">
                        {item.type === 'boy' ? '' : item.type === 'girl' ? '' : ''}
                    </span>
                    {item.type === 'unknown' && <small style={{fontSize:'10px'}}>50/50</small>}
                </div>
            ))}
        </div>
      )}
    </div>
  );
};

export default GenderCalendar;