  //  :   
  const getStartDate = (dobStr, hasSurgery, surgeryDateStr, conceptionDate) => {
      let startDate = new Date(dobStr);
      if (hasSurgery && surgeryDateStr) {
          const surgeryDate = new Date(surgeryDateStr);
          if (surgeryDate < conceptionDate) {
              startDate = surgeryDate;
          }
      }
      return startDate;
  };
  
  //  :  
  const calculateChinese = (momAge, conceptionDate) => {
      let lunarAge = momAge + 1;
      let conceptionMonth = conceptionDate.getMonth();
      
      if (lunarAge < 18 || lunarAge > 45) {
          return { text: " 18-45  ", class: "unknown" };
      }
      
      const res = chineseChart[lunarAge][conceptionMonth];
      return res === 1 
          ? { text: ` (: ${lunarAge})`, class: "boy" } 
          : { text: ` (: ${lunarAge})`, class: "girl" };
  };
  
  // ---    ---
  //        
  export const calculateGenderLogic = (formData) => {
      const { momDob, dadDob, conceptionDate: conceptionStr, momHasSurgery, momSurgeryDate, dadHasSurgery, dadSurgeryDate } = formData;
  
      const conceptionDate = new Date(conceptionStr);
      const momStartDate = getStartDate(momDob, momHasSurgery, momSurgeryDate, conceptionDate);
      const dadStartDate = getStartDate(dadDob, dadHasSurgery, dadSurgeryDate, conceptionDate);
  
      //  
      let dadYearsPassed = conceptionDate.getFullYear() - dadStartDate.getFullYear();
      let momYearsPassed = conceptionDate.getFullYear() - momStartDate.getFullYear();
  
      // 
      if (conceptionDate < new Date(conceptionDate.getFullYear(), dadStartDate.getMonth(), dadStartDate.getDate())) dadYearsPassed--;
      if (conceptionDate < new Date(conceptionDate.getFullYear(), momStartDate.getMonth(), momStartDate.getDate())) momYearsPassed--;
  
      const dadRemainder = dadYearsPassed % 4;
      const momRemainder = momYearsPassed % 3;
  
      //   
      if (dadRemainder < momRemainder) {
          return { resultType: 'boy' };
      } else if (momRemainder < dadRemainder) {
          return { resultType: 'girl' };
      } else {
          // 50/50 
          const momDobDate = new Date(momDob);
          let momRealAge = conceptionDate.getFullYear() - momDobDate.getFullYear();
          if (conceptionDate < new Date(conceptionDate.getFullYear(), momDobDate.getMonth(), momDobDate.getDate())) momRealAge--;
          
          const chineseRes = calculateChinese(momRealAge, conceptionDate);
          
          return {
              resultType: 'unknown',
              details: {
                  bloodText: " 50/50 ( )",
                  chineseText: chineseRes.text,
                  chineseClass: chineseRes.class
              }
          };
      }
  };