import React, { useState, useEffect, useRef } from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { useAuth } from "../context/AuthContext";
import "./header.scss";

import logoImage from "../../assets/img/logo.png";

const Header = () => {
    const location = useLocation();
    const navigate = useNavigate();
    const { user, logout } = useAuth(); //     

    const [mobileNavActive, setMobileNavActive] = useState(false);
    const [showProfileMenu, setShowProfileMenu] = useState(false); //  
    const profileRef = useRef(null); //     

    //   (    )
    const credits = localStorage.getItem("genderApp_credits") || "0";
    const isPremium = localStorage.getItem("genderApp_isPremium") === "true";

    const toggleMobileNav = () => {
        setMobileNavActive(!mobileNavActive);
        document.body.classList.toggle("mobile-nav-active");
    };

    const closeMobileNav = () => {
        setMobileNavActive(false);
        document.body.classList.remove("mobile-nav-active");
    };

    const handleLogout = () => {
        logout();
        setShowProfileMenu(false);
        navigate("/login");
    };

    //    
    useEffect(() => {
        const handleClickOutside = (event) => {
            if (profileRef.current && !profileRef.current.contains(event.target)) {
                setShowProfileMenu(false);
            }
        };
        document.addEventListener("mousedown", handleClickOutside);
        return () => document.removeEventListener("mousedown", handleClickOutside);
    }, []);

    return (
        <header id="header" className="header d-flex align-items-center sticky-top">
            <div className="header-container container-fluid container-xl position-relative d-flex align-items-center justify-content-between">
                <Link
                    to="/"
                    className="logo d-flex align-items-center me-auto me-xl-0"
                    onClick={closeMobileNav}
                >
                    <img src={logoImage} alt="" />
                </Link>

                <nav
                    id="navmenu"
                    className={`navmenu ${mobileNavActive ? "mobile-nav-active" : ""}`}
                >
                    <ul>
                        <li>
                            <Link
                                to="/"
                                className={location.pathname === "/" ? "active" : ""}
                                onClick={closeMobileNav}
                            >
                                
                            </Link>
                        </li>
                        <li>
                            <Link
                                to="/calendar"
                                className={location.pathname === "/calendar" ? "active" : ""}
                                onClick={closeMobileNav}
                            >
                                
                            </Link>
                        </li>
                        <li>
                            <Link
                                to="/planner"
                                className={location.pathname === "/planner" ? "active" : ""}
                                onClick={closeMobileNav}
                            >
                                
                            </Link>
                        </li>
                        <li>
                            <Link
                                to="/pricing"
                                className={location.pathname === "/pricing" ? "active" : ""}
                                onClick={closeMobileNav}
                            >
                                
                            </Link>
                        </li>
                    </ul>
                    <i
                        className={`mobile-nav-toggle d-xl-none bi ${mobileNavActive ? "bi-x" : "bi-list"
                            }`}
                        onClick={toggleMobileNav}
                    ></i>
                </nav>

                {/* ---  :     --- */}
                <div className="header-actions d-flex align-items-center gap-3">
                    {/* <div
                        style={{
                            background: "#05b855ff",
                            color: "#ffffffff",
                            padding: "5px 12px",
                            borderRadius: "20px",
                            fontSize: "13px",
                            fontWeight: "bold",
                            zIndex: 5,
                            boxShadow: "0 2px 5px rgba(0,0,0,0.1)",
                        }}
                    >
                        : {credits} 
                    </div> */}

                    {user ? (
                        <div className="user-profile-section" ref={profileRef}>
                            <div
                                className="profile-trigger d-flex align-items-center gap-2"
                                onClick={() => setShowProfileMenu(!showProfileMenu)}
                            >
                                <div className="avatar-circle">
                                    {user.name ? user.name.charAt(0).toUpperCase() : "U"}
                                </div>
                                {/* <span className="user-name d-none d-md-block">{user.name}</span> */}
                                <i
                                    className={`bi bi-chevron-${showProfileMenu ? "up" : "down"
                                        } small-arrow`}
                                ></i>
                            </div>

                            {/*   */}
                            {showProfileMenu && (
                                <div className="profile-dropdown fade-in-fast">
                                    <div className="dropdown-header">
                                        <strong>{user.email}</strong>
                                        <span
                                            className={`badge ${isPremium ? "bg-warning text-dark" : "bg-info"
                                                }`}
                                        >
                                            {isPremium ? "VIP  " : ""}
                                        </span>
                                    </div>

                                    <div className="dropdown-body">
                                        <div className="info-item">
                                            <span>:</span>
                                            <strong>{credits}</strong>
                                        </div>

                                        <hr />

                                        {/*    " "  */}
                                        {/* <Link to="/profile" className="dropdown-link"><i className="bi bi-person"></i>  </Link> */}

                                        <button
                                            onClick={handleLogout}
                                            className="dropdown-btn text-danger"
                                        >
                                            <i className="bi bi-box-arrow-right"></i> 
                                        </button>
                                    </div>
                                </div>
                            )}
                        </div>
                    ) : (
                        <div className="auth-buttons">
                            <Link to="/login" className="btn-login">
                                
                            </Link>
                        </div>
                    )}
                </div>
            </div>
        </header>
    );
};

export default Header;
