import React, { useState } from "react";
import { calculateGenderLogic } from "../../assets/js/genderLogic";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css"; 
import kaLocale from "../locales/ka";
import "./genderPlanner.scss";

const GenderPlanner = () => {
  const [formData, setFormData] = useState({ 
    momDob: "", 
    dadDob: "", 
    targetYear: new Date().getFullYear().toString()
  });
  const [desiredGender, setDesiredGender] = useState("boy");
  const [planResult, setPlanResult] = useState(null);

  // --- DatePicker-   ---
  const handleDateChange = (date, name) => {
    // Date   'YYYY-MM-DD' 
    const formattedDate = date ? date.toISOString().split('T')[0] : '';
    setFormData(prevData => ({
        ...prevData,
        [name]: formattedDate,
    }));
  };

  // ---   ---
  const handlePlan = () => {
    if (!formData.momDob || !formData.dadDob) return alert("  ");

    const bestDates = [];
    const year = parseInt(formData.targetYear);

    //    12- 
    for (let month = 0; month < 12; month++) {
      //   
      let checkDate = new Date(year, month, 15);

      const tempInput = {
        momDob: formData.momDob,
        dadDob: formData.dadDob,
        conceptionDate: checkDate.toISOString().split('T')[0],
        momHasSurgery: false,
        dadHasSurgery: false
      };

      const res = calculateGenderLogic(tempInput);

      // :  
      let matchScore = 0; 
      
      //     
      if (res.resultType === desiredGender) {
        matchScore = 2; //  
      }
      //  50/50-,   
      else if (res.resultType === 'unknown' && res.details.chineseClass === desiredGender) {
         matchScore = 1; //  
      }

      //   ,  
      if (matchScore > 0) {
          bestDates.push({
              monthName: checkDate.toLocaleString('ka-GE', { month: 'long' }),
              year: year,
              probability: matchScore === 2 ? " (90%)" : " (60%)",
              isBest: matchScore === 2 //  
          });
      }
    }
    setPlanResult(bestDates);
  };

  return (
    <div className="card planner-card fade-in-image">
        <h2 className="text-center"></h2>
        <p className="text-center text-muted">    </p>

        <div className="form-group mb-3">
            <label> :</label>
            <div className="d-flex gap-2">
                {/*  DatePicker */}
                <DatePicker
                    selected={formData.momDob ? new Date(formData.momDob) : null}
                    onChange={(date) => handleDateChange(date, 'momDob')}
                    dateFormat="dd.MM.yyyy"
                    placeholderText="  "
                    locale={kaLocale} 
                    className="form-control"
                    showYearDropdown
                    dropdownMode="select"
                />
                
                {/*  DatePicker */}
                <DatePicker
                    selected={formData.dadDob ? new Date(formData.dadDob) : null}
                    onChange={(date) => handleDateChange(date, 'dadDob')}
                    dateFormat="dd.MM.yyyy"
                    placeholderText="  "
                    locale={kaLocale} 
                    className="form-control"
                    showYearDropdown
                    dropdownMode="select"
                />
            </div>
        </div>

        <div className="form-group mb-3 d-flex justify-content-between align-items-center mt-4">
            <div className="d-flex align-items-center">
                <label className="me-2">:</label>
                <div className="btn-group">
                    <button className={`btn ${desiredGender === 'boy' ? 'btn-boy' : 'btn-outline-boy'}`} onClick={()=>setDesiredGender('boy')}>  </button>
                    <button className={`btn ${desiredGender === 'girl' ? 'btn-girl' : 'btn-outline-girl'}`} onClick={()=>setDesiredGender('girl')}> </button>
                </div>
            </div>
            <div className="d-flex align-items-center">
                <label className="me-2">:</label>
                <input 
                    type="number" 
                    className="form-control d-inline-block" 
                    style={{width: '100px'}} 
                    value={formData.targetYear} 
                    onChange={(e)=>setFormData({...formData, targetYear: e.target.value})} 
                />
            </div>
        </div>

        <button className="btn btn-info w-100 text-white font-weight-bold" onClick={handlePlan}>
              
        </button>

        {/*   */}
        {planResult && (
            <div className="plan-results mt-4">
                <h4>  :</h4>
                {planResult.length === 0 ? (
                    <p className="text-muted">,       .</p>
                ) : (
                    <ul className="list-group">
                        {planResult.map((item, index) => (
                            <li key={index} className={`list-group-item d-flex justify-content-between align-items-center ${item.isBest ? 'list-group-item-success' : ''}`}>
                                <span>
                                    <strong>{item.monthName} {item.year}</strong>
                                    <br/>
                                    <small>{item.isBest ? "  +  ()" : "   "}</small>
                                </span>
                                <span className="badge bg-light text-dark">{item.probability}</span>
                            </li>
                        ))}
                    </ul>
                )}
            </div>
        )}
    </div>
  );
};

export default GenderPlanner;